let countdown;
export default {
	data() {
		return {
			time:0,
			phone:"",
			code:"",
		}
	},
	methods: {
		
		//获取验证码,并且倒计时
		getCode(){
			let that=this;
			if(!that.checkMobile(that.phone)){
				that.Tips({ title: "请输入正确的手机号"});
				return;
			}
			
			if(that.time!=0)return;
			that.time=60;
			that.countdown=setInterval(()=>{
				that.time-=1;
				if(that.time<=0){
					clearInterval(that.countdown)
				}
			},1000);
			
			that.basePost(
				that.U({ c: 'login', a: 'get_update_pass_code'}),
				{
					phone:that.phone,
				},
				function(res) {
					that.Tips({ title: res});
				},
				function(res) {
					console.log(res);
				},
			);
		},
		// 验证验证码，并且跳转重置新密码
		navToResetPwd() {
			let that=this;
			if(that.phone==''){
				that.Tips({ title: "请输入手机号码"});
				return;
			}
			if(that.code==''){
				that.Tips({ title: "请输入验证码"});
				return;
			}
			that.basePost(
				that.U({ c:'user_api', a:'check_phone_code'}),
				{
					code:that.code,
					phone:that.phone,
				},
				function(res) {
					uni.navigateTo({
						url: "/pages/login/reset_password/reset_password?phone="+that.phone
					})
				},
				function(res) {
					that.Tips({ title: res.msg});
				},
			);
		},
	},
}
